import styles from "./Account.module.scss";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import CallApi from "../../../api/callApi";
import { useState, useEffect } from "react";

function DetailOrder() {
  const index = JSON.parse(window.localStorage.getItem("index"));
  const id = JSON.parse(window.localStorage.getItem("id"));
  const [orderUser, setOrderUser] = useState([]);
  useEffect(() => {
    if (parseInt(id) > 0) {
      CallApi(`users/${id}`, "GET", null).then((res) => {
        if (res) {
          setOrderUser(res.data.order);
        } else {
          setOrderUser([]);
        }
      });
    } else {
      setOrderUser([]);
    }
  }, [id]);
  return (
    <>
      {orderUser.length > 0 && (
        <div className={styles.detailOrder}>
          <p className={styles.detailOrderTitle}>
            {" "}
            n hng <span>{orderUser[index].code}</span>  c t vo ngy{" "}
            <span>{orderUser[index].day}</span> v hin ti l{" "}
            <span>{orderUser[index].status}</span>
          </p>
          <h4>Chi tit n hng</h4>
          <div style={{ borderBottom: "3px solid #eee" }}>
            <p>SN PHM</p>
            <p>TNG</p>
          </div>
          {orderUser[index].nameProduct.map((el, index) => (
            <div key={index}>
              <span>
                {el.namePrd} ({el.price} x {el.quantity})
              </span>
              <strong>
                {el.total.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} 
              </strong>
            </div>
          ))}
          <div>
            <strong>Tng s ph:</strong>
            <strong>{orderUser[index].totalPrd} </strong>
          </div>
          <div>
            <strong>Giao nhn hng:</strong>
            <span>Giao hng min ph</span>
          </div>
          <div>
            <strong>Phng thc thanh ton:</strong>
            <span>{orderUser[index].payment}</span>
          </div>
          <div>
            <strong>Tng cng:</strong>
            <strong>{orderUser[index].totalPrd} </strong>
          </div>

          <h4 style={{ marginTop: "20px" }}>a ch thanh ton</h4>
          <section>
            <strong>a ch:</strong>
            <p>{orderUser[index].addressDelivery.addressHome}</p>
          </section>
          <section>
            <strong>S in thoi:</strong>
            <p>{orderUser[index].addressDelivery.phone}</p>
          </section>
          <section>
            <strong>Tn ngi nhn :</strong>
            <p>{orderUser[index].addressDelivery.name}</p>
          </section>
          <Button
            variant="danger"
            style={{ marginBottom: "50px" }}
            as={Link}
            to="/account/order"
          >
            TR LI
          </Button>
        </div>
      )}
      {orderUser.length === 0 && <h1>xinchao</h1>}
    </>
  );
}
export default DetailOrder;
