import styles from "./Account.module.scss";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import CallApi from "../../../api/callApi";
import { useState, useEffect } from "react";

function Dashboard() {
  const id = JSON.parse(window.localStorage.getItem("id"));
  const [emailUser, setEmailUser] = useState("");
  useEffect(() => {
    CallApi(`users/${id}`, "GET", null).then((res) => {
      if (res) {
        setEmailUser(res.data.email);
      }
    });
  }, [id]);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const validName = emailUser.split("@")[0];
  return (
    <>
      <div className={styles.dashboard}>
        <div className={styles.dashboard2}>
          Xin cho <strong>{validName}</strong>
        </div>
        <p>
          T trang qun l ti khon bn c th xem n hng mi, qun l a
          ch giao hng v thanh ton, and sa mt khu v thng tin ti khon.
        </p>
        <div className={styles.dashboardBtn}>
          <Button
            variant="danger"
            className={styles.dashboardBtnBtn1}
            as={Link}
            to="/account/order"
          >
            n hng
          </Button>
          <Button
            variant="danger"
            className={styles.dashboardBtnBtn1}
            as={Link}
            to="/account/dowload"
          >
            Ti xung
          </Button>
          <Button
            variant="danger"
            className={styles.dashboardBtnBtn1}
            as={Link}
            to="/account/address"
          >
            a ch
          </Button>
          <Button
            variant="danger"
            className={styles.dashboardBtnBtn1}
            as={Link}
            to="/account/accountInfor"
          >
            Thng tin ti khon
          </Button>
        </div>
      </div>
    </>
  );
}
export default Dashboard;
